<template>
    <el-card class="materiais-group">
        <el-row class="mb20">
            <el-col :span="7">
                <span>一级分类：</span>
                <el-select v-model="material.level1" placeholder="请选择分类">
                    <!-- @change="classifyChange" -->
                    <!-- TODO: 此处需要循环显示 -->
                    <el-option label="手机" value="手机" :style="{ 'min-width': minWidth + 'px' }"></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="7">
                <span>二级分类：</span>
                <el-select v-model="material.level2" placeholder="请选择分类">
                    <!-- @change="classifyChange" -->
                    <!-- TODO: 此处需要循环显示 -->
                    <el-option label="手机" value="手机" :style="{ 'min-width': minWidth + 'px' }"></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="7">
                <span>物料组：</span>
                <!-- TODO: 清除选项时获取所有数据 -->
                <el-select v-model="material.level3" placeholder="请选择分类" clearable>
                    <!-- @change="classifyChange"
                    @clear="getClassify"-->
                    <!-- TODO: 此处需要循环显示 -->
                    <el-option
                        label="苹果手机 iPhone 11 Pro"
                        value="苹果手机 iPhone 11 Pro"
                        :style="{ 'min-width': minWidth + 'px' }"
                    ></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="3">
                <el-button
                    type="primary"
                    @click="newIncrease"
                    icon="el-icon-circle-plus-outline"
                >新增物料</el-button>
            </el-col>
        </el-row>

        <!-- 表格 -->
        <el-table
            class="fg1"
            :data="tableData.table"
            border
            stripe
            v-loading="!tableData.table.length ? true : false"
        >
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="scm" label="SCM物料 ID" align="center"></el-table-column>
            <el-table-column prop="group" label="物料组名称" align="center"></el-table-column>
            <el-table-column prop="describe" label="物料名称" align="center"></el-table-column>
            <el-table-column prop="source" label="物料来源" width="100px" align="center"></el-table-column>
            <el-table-column prop="number" label="物料 ID" align="center"></el-table-column>
            <el-table-column align="center" label="操作" width="150px">
                <template slot-scope="scope">
                    <el-tooltip content="修改" placement="top" :enterable="false">
                        <el-button
                            type="primary"
                            size="mini"
                            icon="el-icon-edit"
                            @click="modify(scope.row)"
                        ></el-button>
                    </el-tooltip>
                    <el-tooltip content="查看详情" placement="top" :enterable="false">
                        <el-button
                            @click="handleClick(scope.row)"
                            type="warning"
                            size="mini"
                            icon="el-icon-view"
                        ></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="paging mt10">
            <el-pagination
                background
                layout="prev, pager, next, jumper"
                :total="tableData.total"
                @current-change="currentChange"
            ></el-pagination>
        </div>

        <!-- 新增物料 dialog -->
        <el-dialog
            :title="title"
            :visible.sync="dialogFormVisible"
            :show-close="disabled"
            :close-on-click-modal="disabled"
            :close-on-press-escape="disabled"
            @closed="disabled = false"
        >
            <el-form
                :model="material"
                :rules="rules"
                ref="material"
                @submit.native.prevent="onSubmit"
                :disabled="disabled"
            >
                <!-- TODO: 插值表达式中的值应该是获取的 -->
                <el-row class="pl35">
                    <el-col :span="6">
                        <el-form-item label="一级分类：">{{ material.level1 }}</el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="二级分类：">{{ material.level2 }}</el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="二级分类：">{{ material.level3 }}</el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="物料组 ID" label-width="100px">
                    <el-input :value="material.number" :disabled="true"></el-input>
                </el-form-item>

                <el-form-item label="SCM物料 ID" label-width="100px" prop="scm">
                    <el-input v-model="material.scm"></el-input>
                </el-form-item>

                <el-form-item label="颜色" label-width="100px" prop="color">
                    <!-- TODO: COLOR 获取数据并渲染 -->
                    <el-select v-model="material.color" placeholder="请选择物料颜色">
                        <el-option
                            :label="item"
                            :value="item"
                            v-for="item in material.colors"
                            :key="item"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="配置" label-width="100px" prop="configruation">
                    <!-- TODO: CONFIGURATION 获取数据并渲染 -->
                    <el-select v-model="material.configruation" placeholder="请选择物料配置">
                        <el-option
                            :label="item"
                            :value="item"
                            v-for="item in material.configruations"
                            :key="item"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="版本" label-width="100px" prop="version">
                    <!-- TODO: VERSION 获取数据并渲染 -->
                    <el-select v-model="material.version" placeholder="请选择物料版本">
                        <el-option
                            :label="item"
                            :value="item"
                            v-for="item in material.versions"
                            :key="item"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="物料来源" label-width="100px">
                    <el-select v-model="material.source" placeholder="请选择物料来源">
                        <el-option label="集采" :value="0"></el-option>
                        <el-option label="自采" :value="1"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="物料名称" label-width="100px">
                    <el-input :value="material.name" :disabled="true"></el-input>
                </el-form-item>

                <el-form-item label="物料描述" label-width="100px">
                    <el-input v-model="material.describe"></el-input>
                </el-form-item>

                <el-alert
                    title="图片上传说明"
                    type="info"
                    description="上传的第一张图片为主图，最多上传五张图片；建议分辨率：800 * 800；图片大小：不超过 2M"
                    show-icon
                    :closable="false"
                    class="mb15 "
                ></el-alert>

                <el-form-item label="物料组轮播图" label-width="100px" prop="fileList">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :limit='5'
                        :file-list="material.fileList"
                        :on-preview="handlePictureCardPreview"
                        :on-exceed="file => $message.warning('最多上传五张图片')"
                        :on-remove="handleRemove"
                        :on-change="({name, url}) => material.fileList.push({name, url})"
                    >
                        <!-- :on-success="handleSuccess" -->
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog title="图片详情" :visible.sync="details" :append-to-body="true">
                        <img width="100%" :src="detailsImageUrl" alt />
                    </el-dialog>
                </el-form-item>

                <el-form-item style="text-align: right" v-show="!disabled">
                    <el-button @click="cancel">取 消</el-button>
                    <el-button type="primary" native-type="submit">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </el-card>
</template>

<script>
/* el-select BUG */
import { mixin } from "../../mixins/resize";
export default {
    mixins: [mixin],
    data() {
        return {
            /* 是否禁用 */
            disabled: false,
            dialogFormVisible: false,
            /* 图片上传 */
            detailsImageUrl: "",
            details: false,
            /* 表格 */
            tableData: {
                table: [],
                total: null
            },
            /* 新增物料 */
            title: '新增物料',
            material: {
                // TODO: 还差分类循环所需要的数据
                colors: ["绿色", "黄色", "白色"],
                configruations: ["16G", "32G", "64G", "128G"],
                versions: ["aa", "bb", "cc"],
                // 以下才是需要传递的数据
                number: 123456789,
                scm: null,
                level1: "手机",
                level2: "苹果",
                level3: "苹果手机 iPhone 11 Pro",
                color: "",
                configruation: "",
                version: "",
                source: "集采",
                name: "",
                describe: "",
                fileList: []
            },
            rules: {
                scm: [
                    {
                        required: true,
                        message: "SCM物料 ID 不能为空",
                        trigger: "blur"
                    }
                ],
                color: [
                    {
                        required: true,
                        message: "请选择物料颜色",
                        trigger: "change"
                    }
                ],
                configruation: [
                    {
                        required: true,
                        message: "请选择物料配置",
                        trigger: "change"
                    }
                ],
                version: [
                    {
                        required: true,
                        message: "请选择物料配置",
                        trigger: "change"
                    }
                ],
                fileList: [
                    {
                        type: "array",
                        required: true,
                        message: "至少上传一张图片",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    computed: {
        materialName() {
            let {
                material: { level3, color, configruation, version }
            } = this;
            return {
                level3,
                color,
                configruation,
                version
            };
        }
    },
    methods: {
        /**
         *  TODO: 表格数据
         */
        async getManagement() {
            const { res, total } = await this.$get("/management");
            this.tableData.table = res;
            this.tableData.total = total;
        },
        /**
         *  TODO: 分页查询
         */
        async currentChange(value) {
            const { res, total } = await this.$get(`/management?page=${value}`);
            this.tableData.table = res;
            this.tableData.total = total;
        },
        /**
         *  TODO: 查看详情
         */
        handleClick(value) {
            console.log(value);

            this.title = "详情";
            this.disabled = true;
            this.dialogFormVisible = true;
        },
        /**
         *  TODO: 修改
         */
        modify(row) {
            this.title = "物料修改";
            this.dialogFormVisible = true;
        },
        /**
         *  TODO: 点击新增物料
         */
        newIncrease() {

            this.title = '新增物料'
            this.dialogFormVisible = true;
        },
        /**
         *  TODO: 图片展示
         */
        handlePictureCardPreview(file) {
            this.detailsImageUrl = file.url;
            this.details = true;
        },
        /**
         *  图片删除
         */
        handleRemove(file) {
            let {
                material: { fileList }
            } = this;
            let index = fileList.findIndex(({ uid }) => uid === file.uid);
            fileList.splice(index, 1);
        },
        /**
         *  TODO: 取消
         */
        cancel() {
            this.$confirm("确定取消?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.dialogFormVisible = false;
                this.$refs.material.resetFields();
                this.$message({
                    type: "success",
                    message: "已取消"
                });
            });
        },
        /**
         *  TODO: 提交
         */
        onSubmit() {
            this.$refs.material.validate(valid => {
                if (valid) {
                    this.dialogFormVisible = false;
                    this.$refs.material.resetFields();
                    alert("此处提交数据!");
                } else {
                    return false;
                }
            });
            console.log(this.material);
        }
    },
    watch: {
        materialName: {
            handler(newVal, oldVal) {
                let { level3, color, configruation, version } = newVal;
                let str =
                    level3 + " " + color + " " + configruation + " " + version;
                this.$set(this.material, "name", str);
                this.$set(this.material, "describe", str);
            },
            deep: true,
            immediate: true
        }
    },
    created() {
        this.getManagement();
    }
};
</script>

<style scoped>
.paging {
    text-align: right;
}
</style>



